import { types } from '../types/types';

const initialState = {
    checking: true,
    uid: null,
    token: null,
    token_init_date:null

}

export const authReducer = ( state = initialState, action ) => {
    switch ( action.type ) {
        case types.authLogin:
            return {
                ...state,
                ...action.payload,
                checking: false
            }
        case types.authCheckingFinish:
            return {
                ...state,
                checking: false
            }
        case types.authLogout:
            return {
                initialState
            }
        default:
            return state;
    }

}